"use client";

import { useState, useEffect, useRef } from "react";
import axios from "axios";
import { useRouter } from "next/navigation";
import GaugeChart from "react-gauge-chart";

export default function StressMeter({ userId }) {
  const [level, setLevel] = useState(0.5); // target slider
  const [displayLevel, setDisplayLevel] = useState(0.5); // animated needle
  const [saving, setSaving] = useState(false);
  const [emoji, setEmoji] = useState("üòê");
  const router = useRouter();
  const animationRef = useRef();

  // Animate needle
  useEffect(() => {
    const animate = () => {
      setDisplayLevel((prev) => {
        const diff = level - prev;
        if (Math.abs(diff) < 0.001) return level;
        return prev + diff * 0.1;
      });
      animationRef.current = requestAnimationFrame(animate);
    };
    animationRef.current = requestAnimationFrame(animate);
    return () => cancelAnimationFrame(animationRef.current);
  }, [level]);

  // Update emoji
  useEffect(() => {
    if (displayLevel < 0.33) setEmoji("üòä");
    else if (displayLevel < 0.66) setEmoji("üòê");
    else setEmoji("üò¢");
  }, [displayLevel]);

  // Background color based on stress
  const bgColor = () => {
    if (displayLevel < 0.33) return "bg-green-200";
    else if (displayLevel < 0.66) return "bg-yellow-200";
    else return "bg-red-200";
  };

  const handleSave = async () => {
    try {
      setSaving(true);
      const stressLevel = Math.round(level * 10);
      await axios.post("http://localhost:4000/api/stress", { userId, stressLevel });
      alert("Stress level logged successfully ‚úÖ");
      router.push("/student/dashboard");
    } catch (err) {
      console.error(err);
      alert("Error saving stress level ‚ùå");
    } finally {
      setSaving(false);
    }
  };

  return (
    <div className={`flex flex-col items-center justify-center w-full max-w-md rounded-xl p-8 space-y-6 transition-colors duration-500 ${bgColor()}`}>
      <h1 className="text-3xl font-bold text-gray-800 text-center">
        Daily Stress Meter
      </h1>

      {/* Emoji Feedback */}
      <div className="text-6xl animate-bounce">{emoji}</div>

      {/* Gauge Chart */}
      <GaugeChart
        id="animated-gauge"
        nrOfLevels={20}
        colors={[
          "#00FF00", "#33FF00", "#66FF00", "#99FF00",
          "#CCFF00", "#FFFF00", "#FFCC00", "#FF9900",
          "#FF6600", "#FF3300", "#FF0000"
        ]}
        percent={displayLevel}
        arcPadding={0.02}
        needleColor="#333333"
        needleBaseColor="#333333"
        animate={false}
        textColor="#000"
        formatTextValue={() => `${Math.round(displayLevel * 10)} / 10`}
      />

      {/* Slider */}
      <input
        type="range"
        min={0}
        max={1}
        step={0.01}
        value={level}
        onChange={(e) => setLevel(parseFloat(e.target.value))}
        className="w-full h-2 bg-gray-300 rounded-lg accent-indigo-600"
      />

      <p className="text-lg font-semibold">
        Selected Level: {Math.round(level * 10)} / 10
      </p>

      <button
        onClick={handleSave}
        disabled={saving}
        className="w-full bg-indigo-600 hover:bg-indigo-700 text-white font-bold py-3 px-4 rounded-xl shadow-lg disabled:opacity-50 transition-transform transform hover:scale-105"
      >
        {saving ? "Saving..." : "Submit Stress Level"}
      </button>
    </div>
  );
}
